var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"obs-page"},[_c('PhotoHeader',{attrs:{"title":"Evento","description":"Descrio dos eventos da jornada"}}),_c('Main',[_c('Paragraph',{attrs:{"title":"Histrico"}},[_vm._v("\n      A partir de 25 de Junho de 2014 a PUC Gois iniciou uma nova organizao institucional baseada no\n      conceito de Escolas. A ECEC foi criada no dia 24 junho de 2015. A ECEC teve origem da fuso de dois\n      departamentos: o Departamento de Matemtica e Fsica (MAF) e o Departamento de Computao (CMP). A\n      ECEC  responsvel por oferecer os cursos de licenciatura em Fsica, Matemtica e Qumica; os cursos de\n      bacharelado em Cincia da Computao e Engenharia de Computao e o curso Superior de Tecnologia em\n      Anlise e Desenvolvimento de Sistemas.\n      A fuso dos cursos dos antigos departamentos e a interao entre professores e alunos da nova escola\n      motivou a reorganizao dos eventos cientficos organizados pelos antigos departamentos. Nesse sentido, foi\n      criada a Jornada Cintfica da Escola de Cincias Exatas e da Computao (JCECEC) para dar continuidade\n      aos eventos histricos organizados pelo MAF e CMP, tais como a Semana de Computao da Universidade\n      Catlica de Gois (UCG-Comp) organizada pelo CMP, Jornada de Cincias Exatas (Jorcex) organizada pelo\n      MAF.\n      Com o propsito de arganizar o ciclo pedaggico/acadmico completo foi criada tambm a revista\n      cientfica da ECEC que foi denominada Arithms http://seer.pucgoias.edu.br/index.php/arithmos. Assim, os\n      artigos que se destacam nas apresentaes orais da JCECEC so convidados para uma publicao extendida\n      na revista Arithms\n      A JCECEC tem o compromisso histrico de dar continuidade a estes eventos organizados pelos\n      antigos departamentos que deram origem a ECEC. As trs primeiras edies da JCECEC abordaram importantes temas, como a I JCECEC - Desafios da integrao de conhecimentos em cincia e tecnologia, II JCECEC - Cincia e tecnologia no desenvolvimento de um futuro sustentvel e III JCECEC - Cincia,\n      Tecnologia e Mudanas Sociais.\n    ")]),_c('Paragraph',{attrs:{"title":"IV Jornada Cientfica da Escola de Cincias Exatas e da Computao"}},[_vm._v("\n      A IV JCECEC e I Jornada Cientfica Regional (JCR) ser realizada de 11 a 14 de Novembro de\n      2019. A IV JCECEC e I JCR ser a primeira internacional e vai contar com pesquisadores de diferentes paises.\n      Nessa edio conta-se com a participao do Instituto Federal de Gois (IFG). Novas atividades vem sendo criadas a partir de cada edio da JCECEC. Na III JCECEC foi introduzida a HACKATHON PUC GOIS que vai ter sua segunda edio na IV JCECEC. Na IV JCECEC ser\n      introduzida uma competio de robtica com a participao de alunos de escolas pblicas e privadas de\n      primeiro e segundo grau\n    ")]),_c('Columns',[_c('Paragraph',{attrs:{"title":"II HACKATHON PUC GOIS"}},[_vm._v("\n        Hackatons so eventos cada vez mais frequentes nas comunidades tecnolgicas que promovem o trabalho em\n        equipe, divulgando os melhores alunos e oportunizando futuras vagas no mercado de trabalho. Estes eventos\n        tm sido promovidos comumente em grandes capitais brasileiras como So Paulo e Rio de Janeiro e tm\n        como objetivo resolver problemas ligados a tecnologia envolvendo empresas e instituies que desejem\n        apresentar uma demanda a ser solucionada. A Pontifcia Universidade Catlica de Gois deseja com este\n        evento oportunizar ao aluno a experincia de participar de hackatons como preparao para participao em\n        hackthons de mbito nacional e internacional. A iniciativa de promover o I Hackathon da PUC GOIS no\n        mbito da JCECEC tem o propsito de produzir a experincia visando disseminar a ideia no mbito regional.\n      ")]),_c('Paragraph',{attrs:{"title":"I COMPETIO DE ROBTICA"}},[_vm._v("\n        As competies de robs renem construtores de robs que competem para mostrar qual deles \n        capaz de cumprir da melhor forma um determinado objetivo especificado anteriormente  competio. As\n        competies podem ser diferenciadas segundo alguns critrios bsicos, a saber: propsitos cientficos e\n        acadmicos, grau de autonomia exigida dos prottipos de robs para a realizao das tarefas a eles\n        solicitadas, restries para a construo do prottipo, ou seja, o quo livre  a escolha das plataformas\n        robticas que podem ser empregadas na construo dos prottipos e Ua maneira como a organizao da\n        equipe est vinculada a iniciativas de instituies educacionais ou organizaes formais. A competio de\n        robs da IV JCECEC tem vai explorar dois dos critrios propsitos cientficos e acadmicos, grau de\n        autonomia exigida dos prottipos de robs para a realizao das tarefas a eles solicitadas.\n      ")])],1)],1),_c('Organization'),_c('Main',[_c('Paragraph',{attrs:{"title":"palestrantes"}},[_c('Speakers',{attrs:{"speakers":_vm.speakers,"paginate":1}})],1),_c('Supporters')],1)],1)}
